import axios from 'axios';
const baseUrl = "https://api.giphy.com/v1/gifs/search?api_key=zqTAeHN7pWw8bKG65bMtdR3W7pDKKUEF&q=";
const endUrl = "&limit=25&offset=0&rating=G&lang=en";

class getGif {
  static getSearchResult(query){
    const uri = baseUrl + query +endUrl
    return new Promise((resolve, reject) => {
      axios.get(uri).then((res) => {
        const body = res.data
        // console.log(body.data)
        resolve(body.data)
      }).catch(err => {
        // console.log(err)
        reject(err)
      })
    })
  }

  // get trending gifs
  static getTrending(){
    return new Promise((resolve, reject) => {
      axios.get('https://api.giphy.com/v1/gifs/trending?api_key=zqTAeHN7pWw8bKG65bMtdR3W7pDKKUEF&limit=25&rating=G').then((res) => {
        const body = res.data
        resolve(body.data)
      }).catch((err) => {
        console.log(err)
        reject(err)
      })
    })
  }
}

export default getGif
