<template lang="html">
  <div class="container-fluid">
    <div class="row text-center">
      <div class="col">
        <h1>GIFlab</h1>
        <div class="row text-center">
          <div class="col-12">
            <div class="card text-center m-3">
              <div class="card-header">
                <div class="form-inline">
                  <div class="input-group input-group-sm col-9">
                    <div class="input-group-prepend">
                      <span class="input-group-text" id="inputGroup-sizing-sm">GIF Search</span>
                    </div>
                    <input v-model="searchGif" @keyup.enter="search()" type="text" class="form-control" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
                  </div>
                  <button @click="search()" class="col-3 btn btn-outline-success ml-auto btn-sm">Search!</button>
                </div>
              </div>
              <div v-show="gifList !== []" class="card-body">
                <div class="container-fluid">
                  <div class="row text-center">
                    <div @click="redirectNew(item.url)" class="col-xs-6 col-md-4" v-for="item in pageOfItems" :key="item.id">
                      <div class="card">
                        <figure>
                          <img class="card-img-top" :src="item.images.downsized.url" alt="gif">
                        </figure>
                        <div class="card-body">
                          <div class="card-title">
                            {{ item.title }}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div v-show="gifList !== []" class="card-footer pb-0 pt-3">
                <jw-pagination :items="gifList" :pageSize="pageSize" @changePage="onChangePage"></jw-pagination>
            </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <b-modal ref="my-modal" hide-footer hide-header>
      <div class="container-fluid">
        <div class="row text-center">
          <div class="col">
            <div class="loadingIcon">
              <b-icon icon="arrow-clockwise" animation="spin" font-scale="4"></b-icon>
            </div>
            <div class="modalTitle">
              Loading...
            </div>
          </div>
        </div>
      </div>
  </b-modal>
  </div>
</template>

<script>
import getGif from '../getGif'

export default {
  name: "gifSearch",
  data() {
    return {
      gifList: [],
      itemList: [],
      searchGif: "",
      pageOfItems: [],
      pageSize: 9
    }
  },
  methods: {
    onChangePage(pageOfItems) {
      this.pageOfItems = pageOfItems
    },
    redirectNew(url) {
      window.open(url, '_blank')
    },
    async search() {
      try {
        this.$refs['my-modal'].show()
        this.gifList = await getGif.getSearchResult(this.searchGif);
        this.$refs['my-modal'].hide()
      } catch (err) {
        console.log(err)
      }
    }
  },
  async created() {
    try {
      this.gifList = await getGif.getTrending();
    } catch (err) {
      console.log(err)
    }
  }
}
</script>

<style lang="css" scoped>
</style>
